<template>
  <div id="app">
    <main>
      <header> 
        Spark player
      </header>
      <div class="playing-song-ui">
        <div class="ui-cover">
          <img :src="currentSongCover" class="ui-cover-img" alt="...">
        </div>

        <p>{{ currentSong.title }}</p>

        <div class="controls">
          <button @click="prev">
            <img src="@/assets/skip-back.svg" alt="">
          </button>

          <button
            v-if="isPLaying"
            @click="pause">
            <img src="@/assets/pause.svg" alt="">
          </button>

          <button
            v-if="!isPLaying"
            @click="playPausedSong">
            <img src="@/assets/play.svg" alt="">
          </button>

          <button @click="next">
            <img src="@/assets/skip-forward.svg" alt="">
          </button>
        </div>
      </div>

      <ul>
        <li v-for="(song, key) in playlist"
          :key="playlist.key"
          @click="playSong(song)">
          <p class="title">{{ song.title }}</p>
          <div class="cover">
            <img :src="song.cover" alt="..." class="cover-img">
          </div>
        </li>

      </ul>
      <small>Contact the developer @langfordquarshie21@gmail.com</small>
    </main>
  </div>
</template>

<script>
import "@/styles/style.css"

export default {
  name: "app",

  data () {
    return {
      currentSong: {},
      player: new Audio(),
      currentSongCover: '',
      isPLaying: false,
      index: 0,

      playlist: [
        {id: '1', src: require('@/assets/songs/6.mp3'), cover: require('@/assets/covers/6.jpg'), title: 'Avicii - The Nights', isPLaying: false},
        {id: '2', src: require('@/assets/songs/7.mp3'), cover: require('@/assets/covers/7.jpg'), title: 'Tick tock - Clean Bandit, Mabel ft. 24kGold', isPLaying: false},
        {id: '3', src: require('@/assets/songs/4.mp3'), cover: require('@/assets/covers/4.jpg'), title: ' Juice WRLD & Seezyn - Hide', isPLaying: false},
        {id: '4', src: require('@/assets/songs/5.mp3'), cover: require('@/assets/covers/5.jpg'), title: 'Carry Your Throne - Jon Bellion', isPLaying: false},
        {id: '5', src: require('@/assets/songs/7.mp3'), cover: require('@/assets/covers/7.jpg'), title: 'Tick tock - Clean Bandit, Mabel ft. 24kGold', isPLaying: false},
        {id: '6', src: require('@/assets/songs/5.mp3'), cover: require('@/assets/covers/5.jpg'), title: 'Carry Your Throne - Jon Bellion', isPLaying: false}
      ]
    }
  },

  mounted () {
    this.loadSongs()
  },

  methods: {

    loadSongs () {
      this.currentSong = this.playlist[0]
      this.currentSongCover = this.playlist[0].cover
      this.player.src = this.playlist[0].src
    },

    setCurrentSong () {
      this.currentSong = this.playlist[this.index]
      this.player.src = this.currentSong.src
      this.currentSongCover = this.currentSong.cover
      this.player.play()
      this.isPLaying = true
    },

    playSong (song) {
      this.currentSong = song
      this.player.src = song.src
      this.currentSongCover = song.cover

      this.player.play()
      this.isPLaying = true
    },

    playPausedSong () {
      this.player.play()
      this.isPLaying = true
    },

    pause () {
      this.player.pause()
      this.isPLaying = false
    },

    next () {
      this.currentSong.isPLaying = false
      this.index = this.playlist.indexOf(this.currentSong)
      this.index++


      if (this.index > this.playlist.length - 1) {
        this.index = 0
      }

      this.setCurrentSong();
    },

    prev () {
      this.currentSong.isPLaying = false
      this.index = this.playlist.indexOf(this.currentSong)
      this.index--


      if (this.index < 0) {
        this.index = this.playlist.length - 1
      }

      this.setCurrentSong();
    }
  }
}

</script>
